$likely-sz: 15px; //Likely Font Size

.likely, .likely__widget {
	padding: 0;
	text-indent: 0 !important;
	list-style: none !important;
	font-weight: 400;
	font-family: Helvetica Neue,Arial,sans-serif;
}

.likely {
	display: inline-block;
}

.likely__widget {
	margin: 0;
	font-size: inherit;
}

.likely {
	opacity: 0;
	font-size: 0 !important;
}

.likely_visible {
	opacity: 1;
	transition: opacity .1s ease-in;
}

.likely > * {
	display: inline-block;
	visibility: hidden;
}

.likely_visible > * {
	visibility: inherit;
}

.likely__widget {
	position: relative;
	white-space: nowrap;
}

.likely__button, .likely__widget {
	display: inline-block;
}

.likely__button, .likely__counter {
	text-decoration: none;
	text-rendering: optimizeLegibility;
	margin: 0;
	outline: 0;
}

.likely__button {
	position: relative;
	user-select: none;
}

.likely__counter {
	display: none;
	position: relative;
	font-weight: 400;
}

.likely_ready {
	.likely__counter {
		display: inline-block;
	}
	.likely__counter_empty {
		display: none;
	}
}


.likely__button, .likely__counter, .likely__icon {
	vertical-align: top;
}

.likely__widget {
	vertical-align: top;
	transition: background .33s ease-out,color .33s ease-out,fill .33s ease-out;
	&:active, &:focus, &:hover {
		transition: none;
		cursor: pointer;
	}
}


@media (hover: none) {
	.likely__widget{
		&:active, &:focus, &:hover {
			transition: background .33s ease-out,color .33s ease-out,fill .33s ease-out;
			cursor: unset;
		}
	}
}

.likely__icon {
	position: relative;
	text-align: left;
	display: inline-block;
}

.likely svg {
	position: absolute;
	left: 3px;
	top: 3px;
	width: 16px;
	height: 16px;
}

.likely__button, .likely__counter {
	line-height: inherit;
	cursor: inherit;
}

.likely__button:empty {
	display: none;
}

.likely__counter {
	text-align: center;
}

.likely {
	.likely__widget {
		color: #000;
		background: rgba(231, 231, 231, 0.8);
	}
	.likely__counter {
		background-size: 1px 1px;
		background-repeat: repeat-y;
		background-image: linear-gradient(to right, rgba(0, 0, 0, 0.2) 0, transparent 0.5px, transparent 100%);
	}
}


.likely-light {
	.likely__widget {
		color: #fff;
		fill: #fff;
		background: rgba(236, 236, 236, 0.16);
		text-shadow: rgba(0, 0, 0, 0.2) 0 0 0.33em;
	}
	.likely__counter {
		background-image: linear-gradient(to right, rgba(255, 255, 255, 0.4) 0, rgba(255, 255, 255, 0) 0.5px, rgba(255, 255, 255, 0) 100%);
	}
}


