.likely {
	margin: 0 -5px -10px;
	.likely__widget {
		line-height: 20px;
		font-size: 14px;
		border-radius: 3px;
		height: 22px;
		margin: 0 5px 10px;
	}
	.likely__icon {
		width: 22px;
		height: 22px;
	}
	.likely__button {
		padding: 1px 4px 1px 0;
	}
	.likely__counter {
		padding: 1px 4px;
	}
}


.likely-big {
	margin: 0 -6px -12px;
	.likely__widget {
		line-height: 28px;
		font-size: 18px;
		border-radius: 4px;
		height: 32px;
		margin: 0 6px 12px;
	}
	.likely__icon {
		width: 32px;
		height: 32px;
	}
	.likely__button {
		padding: 2px 6px 2px 0;
	}
	.likely__counter {
		padding: 2px 6px;
	}
	svg {
		left: 4px;
		top: 4px;
		width: 24px;
		height: 24px;
	}
}


.likely-small {
	margin: 0 -4px -8px;
	.likely__widget {
		line-height: 14px;
		font-size: 12px;
		border-radius: 2px;
		height: 18px;
		margin: 0 4px 8px;
	}
	.likely__icon {
		width: 18px;
		height: 18px;
	}
	.likely__button {
		padding: 2px 3px 2px 0;
	}
	.likely__counter {
		padding: 2px 3px;
	}
	svg {
		left: 3px;
		top: 3px;
		width: 12px;
		height: 12px;
	}
}


.likely {
	top: -10px;
	position: relative;
	.likely__widget {
		border-radius: 0;
		padding: 3px 3px 25px 6px;
		font-size: $likely-sz;
	}
	.likely__counter {
		padding: 1px 4px 1px 6px;
	}
	.likely__button {
		margin-left: 2px;
		opacity: .8;
		padding: 1px 8px 1px 1px;
	}
}