.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/* ========================= */
::placeholder {
  color: #666;
}

::selection {
  background-color: #EC1C1C;
  color: #fff;
}

html, body {
  height: 100%;
}

body {
  font-size: 1.125rem;
  min-width: 320px;
  position: relative;
  line-height: 1.5;
  font-family: "roboto-g", sans-serif;
  overflow-x: hidden;
  color: #333;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.video {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
}

.video .iframe {
  border: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}

.text-center {
  text-align: center;
}

a {
  color: #EC1C1C;
  text-decoration: underline;
}

a:hover, a:focus {
  color: #EC1C1C;
}

/* ==== CONTAINER-FLUID ==== */
.container-fluid {
  max-width: 1200px;
  padding: 0 20px;
}

/* ==== SECTION ============ */
.section {
  background-color: #fafafa;
  padding-bottom: 20px;
}

.section__headline {
  text-transform: uppercase;
  margin: 0;
  font-size: 1.4375rem;
  font-weight: 800;
}

/* ==== Always at the end == */
/*==========  Desktop First  ==========*/
@media (max-width: 991.98px) {
  body {
    font-size: 90%;
  }
  .mobile-menu {
    position: absolute;
    width: 100%;
    z-index: 100;
  }
  .mobile-menu-button {
    display: block;
  }
  .main-menu {
    display: none;
    padding: 20px;
  }
  .item-title__content {
    padding: 40px 20px;
  }
  .second-section,
  .third-section {
    padding: 10px 0;
  }
  .third-section {
    padding: 20px 0 30px;
  }
  .third-section > div > div > div:last-of-type .col-item {
    border-bottom: 0;
    padding-bottom: 0;
    margin-bottom: 0;
  }
  .footer .mobile-menu-button {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .top-ad {
    padding-top: 0;
    padding-bottom: 10px;
  }
  .section-header .top-ad {
    padding-top: 10px;
  }
  .first-section {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .container-fluid {
    padding: 0 10px;
  }
  .no-gutters .pattern {
    margin-bottom: 10px;
  }
  .item-news {
    margin-bottom: 10px;
  }
  .third-section .item-col {
    margin-bottom: 10px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  }
  .third-section .item-col__meta, .third-section .item-col__title {
    padding: 0 20px;
  }
  .third-section .item-col__img {
    margin-bottom: 10px;
  }
  .third-section .item-col__meta {
    margin-bottom: 5px;
  }
  .fourth-section {
    padding: 13.33333px 0;
  }
  .fifth-section .pattern,
  .cat-first .pattern {
    margin-bottom: 10px;
  }
  .header-cat-block {
    padding: 50px;
  }
  .header-cat-block__headline {
    font-size: 28px;
  }
  .isPage .page-header-meta__headline {
    font-size: 24px;
  }
  .isPage .page-header.overlay::before {
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.75));
  }
  .article-main__blockquote {
    margin: 0;
  }
}

@media (max-width: 575.98px) {
  .logo {
    font-size: 19px;
    padding-left: 10px;
  }
  .search-wrap__btn {
    padding-right: 10px;
  }
  .pagination__active, .pagination__link, .pagination__left, .pagination__right {
    width: 36px;
    height: 36px;
    line-height: 34px;
    margin: 2px;
  }
  .pagination__left, .pagination__right {
    font-size: 24px;
    line-height: 22px;
    width: 24px;
  }
  .footer .social-menu {
    padding: 10px 0 0;
  }
  .footer .social-menu__link {
    width: 48px;
    height: 48px;
    line-height: 46px;
    font-size: 22px;
  }
}

