  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug-grid > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: 1;
  height: 1;
  height: inherit;
  outline: #000;
  background: rgba(255, 0, 0, 0.2);
}

