.pagination__active, .pagination__link {
  border: 2px solid #7E7E7E;
  border-radius: 10em;
}

.pagination__active {
  border: 2px solid #EC1C1C;
  color: #EC1C1C;
}

.pagination__left, .pagination__right {
  margin: 0;
  font-size: 32px;
  line-height: 34px;
}

.pagination__left:hover, .pagination__right:hover {
  border: none;
}

.s-more {
  padding: 40px 0;
  background-color: #f5f5f5;
}

.s-more-first {
  padding: 20px 0;
  margin-bottom: 20px;
  background: none;
  border-bottom: 1px solid #dfdfdf;
}

.pattern {
  background-color: #202225;
  position: relative;
  display: block;
}

.pattern::before {
  content: "";
  transition: opacity .25s ease, top .25s ease,  left .25s ease, bottom .25s ease, right .25s ease;
  z-index: 1;
  position: absolute;
  border: #fff solid 1px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
}

.pattern:hover::before {
  opacity: 0.2;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
}

.pattern:hover .overlay::before {
  opacity: 0.75;
}

.search-wrap__btn {
  position: absolute;
  right: 0;
  color: #5A5A5A;
  cursor: pointer;
  line-height: 65px;
  text-align: center;
  transition: color .25s ease;
  padding-right: 20px;
  padding-left: 10px;
}

.search-wrap__btn:hover {
  color: #747474;
}

.search-wrap__field {
  display: none;
  position: absolute;
  left: 0;
  top: 70px;
  padding: 15px;
  background-color: #272a2d;
  width: 100%;
  border-top: #313438 1px solid;
  z-index: 4;
}

.second-section {
  padding: 10px 0;
}

.second-section .content-section__headline {
  text-transform: uppercase;
}

.news-wrap {
  padding-bottom: 10px;
}

.news-wrap .pattern::before {
  display: none;
}

.news-wrap .overlay::before {
  opacity: 0.4;
}

.section-header .all-posts-header {
  font-size: 21px;
  font-weight: 800;
  line-height: 1.75;
  text-transform: uppercase;
}

.social {
  margin-top: 10px;
}

.social .fa {
  font-size: .75rem;
}

.social .comments {
  display: inline-block;
}

.social .comments-info {
  display: inline-block;
}

.social .comments-info__link {
  font-size: .75rem;
  display: inline-block;
  margin-left: 10px;
  text-decoration: none;
}

.social .comments__date {
  font-size: .75rem;
  font-weight: bold;
}

.social .comments__info_noDate, .social .comments__info_dateAfter, .social .comments__info_dateBefore {
  position: relative;
}

.social .comments__info_noDate::after, .social .comments__info_dateAfter::after, .social .comments__info_dateBefore::after {
  content: "";
  font-family: fontawesome;
  font-size: 5px;
  top: 45%;
  position: absolute;
  opacity: .5;
}

.social .comments__info_noDate, .social .comments__info_dateAfter {
  margin-right: 25px;
}

.social .comments__info_noDate::after, .social .comments__info_dateAfter::after {
  right: -15px;
}

.social .comments__info_dateBefore {
  margin-left: 20px;
}

.social .comments__info_dateBefore::after {
  left: -25px;
  margin-left: 10px;
}

.social-menu {
  padding: 15px 0 20px;
}

.social-menu__body, .social-menu__item {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.social-menu__item {
  display: inline-block;
}

.social-menu__link {
  display: block;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10em;
  width: 52px;
  height: 52px;
  line-height: 50px;
  color: #ffffff;
  font-size: 24px;
  margin: 4px;
  transition: border-color .25s ease, opacity .25s ease;
}

.social-menu__link .fa {
  opacity: .75;
  transition: opacity .25s ease;
}

.social-menu__link:hover {
  color: #ffffff;
  border-color: rgba(255, 255, 255, 0.35);
}

.social-menu__link:hover .fa {
  opacity: 1;
}

