  background-color: #202225;
}

.dark-section .section__link {
  color: #ffffff;
}

.top-line {
  background-color: #202225;
  position: relative;
  height: 65px;
}

.top-line .container-fluid {
  position: relative;
}

.isPage .top-line {
  position: absolute;
  z-index: 1;
  width: 100%;
