  display: inline-block
}
.social .comments-info__link {
  font-size: .75rem;
  display: inline-block;
  margin-left: 10px;
  text-decoration: none
}
.social .comments__date {
  font-size: .75rem;
  font-weight: 700
}
.social .comments__info_dateAfter,
.social .comments__info_dateBefore,
.social .comments__info_noDate {
  position: relative
}
.social .comments__info_dateAfter::after,
.social .comments__info_dateBefore::after,
.social .comments__info_noDate::after {
  content: "";
  font-family: fontawesome;
  font-size: .3125rem;
  top: 45%;
  position: absolute;
  opacity: .5
}
.social .comments__info_dateAfter,
.social .comments__info_noDate {
  margin-right: 25px
}
.social .comments__info_dateAfter::after,
.social .comments__info_noDate::after {
  right: -15px
}
.social .comments__info_dateBefore {
  margin-left: 20px
}
.social .comments__info_dateBefore::after {
  left: -25px;
  margin-left: 10px
}
.social-menu {
  padding: 15px 0 20px
}
.social-menu__body,
.social-menu__item {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center
}
.social-menu__item {
  display: inline-block
}
.social-menu__link {
  display: block;
  text-align: center;
  border: 1px solid rgba(255,255,255,.1);
  -webkit-border-radius: 10em;
  border-radius: 10em;
  width: 52px;
  height: 52px;
  line-height: 3.125rem;
  color: #fff;
  font-size: 1.5rem;
  margin: 4px;
  -webkit-transition: border-color .25s ease,opacity .25s ease;
  -o-transition: border-color .25s ease,opacity .25s ease;
  transition: border-color .25s ease,opacity .25s ease
}
.social-menu__link .fa {
  opacity: .75;
  -webkit-transition: opacity .25s ease;
  -o-transition: opacity .25s ease;
  transition: opacity .25s ease
}
.social-menu__link:hover {
  color: #fff;
  border-color: rgba(255,255,255,.35)
}
.social-menu__link:hover .fa {
  opacity: 1
}
.dark-section {
  padding: 30px 0;
  background-color: #202225
}
.dark-section .section__link {
  color: #fff
}
.top-line {
  background-color: #202225;
  position: relative;
  height: 65px
}
.top-line .container-fluid {
  position: relative
}
.isPage .top-line {
  position: absolute;
  z-index: 1;
  width: 100%
}
